@import url('./Common.css');

#treproject{
    width: 100%; height: 100%;
    margin-top: 100px;
  }
  #treplist{
  text-align: end;
  margin-right: 12%;
  padding: 20px 0;
  }
  #treplist a{
    background-color: #a8766d;
    border-radius: 12px;
    padding: 8px 50px;
    display: inline-block;
    color: #fff;
    font-size: 0.8em
  }
  #treplist a b{
    margin-left: 20px;
  }
  
  #trepmain{
    width: 100%;
    height:500px;
    margin-bottom: 50px;
  }
  #trepmain::after{
    content: '';
    display: inline-block;
    border-bottom: 2px solid #5E6554;
    width: 60%;
    margin:0 20%;
    margin-top: 40px;
  }
  #trepleft{
    float: left;
    margin-left:12%;
    margin-top:2%;
    width: 40%;
  }
  #trepleft img{
    width:580px; height: 350px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #trepleft p{
    color: #5E6554;
    text-align: end;
    padding:10px 50px 20px;
    font-size: 1.2em;
  }
  #trepright{
    float: right;
    margin-right:12%;
    width:30%;
  }
  #treprighth1{
    font-size:2.2em;
    text-align: end;
    color: #a8766d;
  }
  #treprighth1 b{
    font-size:1.8em;
  }
  #treprighttxt{
  width: 100%;
  margin-top: 30px;
  }
  
  #treprighttxt div{
    margin-right: 10px;
    border-bottom: 1px solid #5E6554;
    text-align: end;
    padding: 20px 0;
  }
  #treprighttxt p{
    color: #5E6554;
    font-size: 1.2em;
  }
  #treprighttxt p a{
    margin-left: 20px;
    transform: rotate(90deg);
  }
  #treprighttxt span{
    color: #5E6554;
    font-size: 1em;
    padding-right: 20px;
    line-height: 2;
    display: none;
  }
  
  #trepsub{
    width: 100%; height: 100%;
  }
  
  #tresuba{
    width: 100%; height: 500px;
    padding-top: 100px;
  }
  #tresubaleft{
    float: left;
    text-align: start;
    width: 30%;
    margin-top: 12%;
    margin-left: 20%;
  }
  #tresubaleft ul{
    display: flex;
    padding:0;
  }
  #tresubaleft ul li{
    background-color: #5E6554;
    width:40px; height: 40px;
    margin-right: 10px;
  }
  #tresubaleft ul li:nth-of-type(2){
    background-color:#a8766d;
  }
  #tresubaleft ul li:nth-of-type(3){
    background-color:#9A9B72;
  }
  #tresubaleft p{
    margin-top: 10px;
    color: #5E6554;
    font-size: 1.2em;
    line-height: 2;
  }
  #tresubaleft span{
    color: #5E6554;
    line-height:1.5;
    font-size: 0.8em;
  }
  #tresubaright{
    float: right;
    width: 35%;
    margin-right: 12%;
    text-align: end;
  }
  #tresubaright img{
    width:450px; height: 300px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #tresubtxt {
   width:50%;
   margin: 0 auto;
   padding: 30px 10px;
  }
  #tresubtxt p{
    text-align: center;
    color: #9A9B72;
    line-height: 2;
    font-size: 0.8em;
  }
  
  #tresubb{
    width: 100%;
    padding-top: 150px;
  }
  #tresubbleft{
    width:35%;
    float: left;
    margin-left: 5%;
    text-align: end;
  }
  #tresubbleft img{
    width:500px; height: 300px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #tresubblefttxt{
    width:60%;
    padding: 20px 10px;
    text-align: end;
    margin-left:30%;
  }
  #tresubblefttxt p{
    color: #5E6554;
    font-size: 1.2em;
    line-height: 2;
  }
  #tresubblefttxt span{
    color: #9A9B72;
    font-size: 0.8em;
  }
  #tresubbright{
    width: 50%;
    float: right;
    padding-top:200px;
    text-align: end;
    margin-right: 5%;
    margin-bottom: 200px;
  }
  #tresubbright img{
    width:650px; height: 400px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  