@import url('./Common.css');

#listtit{
    width: 100%;
    padding-top: 180px;
    margin-bottom: 100px;
}
#listtit h1{
    text-align: center;
    color:#a8766d;
    font-size: 3em;
}
#listtit h1::after{
    content: ''; clear: both;
    display: block;
    border-bottom:3px solid #a8766d;
    padding-top:10px;
    width:54%;
}
#listcon{
    width: 100%;
}
#listcon ul{
    width: 80%;
    margin: 0 auto;
    padding:0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    overflow: hidden;
}
#listcon ul li{
    width:46%; height: 100%;
    margin-bottom:10%;
}
#listcon ul li figure{
    margin: 0;
    position: relative;
    width: 100%;
}
#listcon ul li figure img{
    width:520px; height:350px;
    border: 10px solid #a8766d;
    border-radius:30px;
    box-sizing: initial;
    overflow: hidden;
}
#listcon ul li figure p{    
    position: absolute;
    left: 0; top: 0;
    background-color: rgba(255,255,255,0.8);
    width:540px; height:370px;
    border-radius: 30px;
    margin: 0;
    z-index: 2;
    opacity: 0;
}
#listcon ul li:hover figure p{
    opacity: 1;
}
#listcon ul li figure p a{
    position: absolute;
    left: 50%; top: 50%;
    transform: translate(-50%,-50%);
    background-color: #a8766d;
    color: #fff;
    padding: 5px 30px;
    font-size: 1em;
    border-radius: 10px;
}
#listcon ul li div{ 
    position: relative;
    width: 50%;
    margin-left: 40%;
    padding: 20px 0;
    text-align: end;
}
#listcon ul li div::after{
    content: '';
    position: absolute;
    left:5%; top:20%;
    border-right:3px solid #a8766d;
    width: 100%; height:65%;
}
#listcon ul li div h2{
    color: #a8766d;
    padding: 10px 0;
}
#listcon ul li div p{
    color: #a8766d;
    font-size: 0.8em;
    line-height: 2;
}
#listmore{
    text-align: center;
    padding: 100px 0;
}
#listmore span{
    background-color: #a8766d;
    color: #fff;
    padding:6px 50px;
    border-radius: 10px;
}
#listmore span a{
    color: #fff;
}
