#intro2{
    width: 100%;
    padding: 100px 0;
    position: relative;
}
#intro2 h2{
    color: #F93700;
    font-size:1.6em;
    position: absolute;
    left:10%; bottom:23%;
    -webkit-transform: rotate(-90deg) translateY(100%);
            transform: rotate(-90deg) translateY(100%);
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
}
#intro2 h2 span{
    display: block;
    border-bottom: 2px solid #a8766d;
    width:100%;
    margin-top: 10px;

}
