@import url('./Common.css');

#twoproject{
    width: 100%; height: 100%;
    margin-top: 100px;
  }
  #twoplist{
  text-align: end;
  margin-right: 12%;
  padding: 20px 0;
  }
  #twoplist a{
    background-color: #5E6554;
    border-radius: 12px;
    padding: 8px 50px;
    display: inline-block;
    color: #fff;
    font-size: 0.8em
  }
  #twoplist a b{
    margin-left: 20px;
  }
  
  #twopmain{
    width: 100%;
    height:500px;
    margin-bottom: 50px;
  }
  #twopmain::after{
    content: '';
    display: inline-block;
    border-bottom: 2px solid #5E6554;
    width: 60%;
    margin:0 20%;
    margin-top: 40px;
  }
  #twopleft{
    float: left;
    margin-left:12%;
    margin-top:2%;
    width: 40%;
  }
  #twopleft img{
    width:580px; height: 350px;
    border:10px solid #5E6554;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #twopleft p{
    color: #5E6554;
    text-align: end;
    padding:10px 50px 20px;
    font-size: 1.2em;
  }
  #twopright{
    float: right;
    margin-right:12%;
    width:30%;
  }
  #twoprighth1{
    font-size:2.2em;
    text-align: end;
    color: #5E6554;
  }
  #twoprighth1 b{
    font-size:1.8em;
  }
  #twoprighttxt{
  width: 100%;
  margin-top: 30px;
  }
  
  #twoprighttxt div{
    margin-right: 10px;
    border-bottom: 1px solid #5E6554;
    text-align: end;
    padding: 20px 0;
  }
  #twoprighttxt p{
    color: #5E6554;
    font-size: 1.2em;
  }
  #twoprighttxt p a{
    margin-left: 20px;
    transform: rotate(90deg);
  }
  #twoprighttxt span{
    color: #5E6554;
    font-size: 1em;
    padding-right: 20px;
    line-height: 2;
    display: none;
  }
  
  #twopsub{
    width: 100%; height: 100%;
  }
  
  #twosuba{
    width: 100%; height: 500px;
    padding-top: 100px;
  }
  #twosubaleft{
    float: left;
    text-align: start;
    width: 30%;
    margin-top: 12%;
    margin-left: 20%;
  }
  #twosubaleft ul{
    display: flex;
    padding:0;
  }
  #twosubaleft ul li{
    background-color: #5E6554;
    width:40px; height: 40px;
    margin-right: 10px;
  }
  #twosubaleft ul li:nth-of-type(2){
    background-color:#a8766d;
  }
  #twosubaleft ul li:nth-of-type(3){
    background-color:#9A9B72;
  }
  #twosubaleft p{
    margin-top: 10px;
    color: #5E6554;
    font-size: 1.2em;
    line-height: 2;
  }
  #twosubaleft span{
    color: #5E6554;
    line-height:1.5;
    font-size: 0.8em;
  }
  #twosubaright{
    float: right;
    width: 35%;
    margin-right: 12%;
    text-align: end;
  }
  #twosubaright img{
    width:450px; height: 300px;
    border:10px solid #5E6554;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #twosubtxt {
   width:50%;
   margin: 0 auto;
   padding: 30px 10px;
  }
  #twosubtxt p{
    text-align: center;
    color: #9A9B72;
    line-height: 2;
    font-size: 0.8em;
  }
  
  #twosubb{
    width: 100%;
    padding-top: 150px;
  }
  #twosubbleft{
    width:35%;
    float: left;
    margin-left: 5%;
    text-align: end;
  }
  #twosubbleft img{
    width:500px; height: 300px;
    border:10px solid #5E6554;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #twosubblefttxt{
    width:60%;
    padding: 20px 10px;
    text-align: end;
    margin-left:30%;
  }
  #twosubblefttxt p{
    color: #5E6554;
    font-size: 1.2em;
    line-height: 2;
  }
  #twosubblefttxt span{
    color: #9A9B72;
    font-size: 0.8em;
  }
  #twosubbright{
    width: 50%;
    float: right;
    padding-top:200px;
    text-align: end;
    margin-right: 5%;
    margin-bottom: 200px;
  }
  #twosubbright img{
    width:650px; height: 400px;
    border:10px solid #5E6554;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  