@import url('./Common.css');

#intro{
    width: 100%; height: 100%;
    margin-top: 80px;
    background-color: #fff;
}
#imain{
    width: 100%; height: 100%;
    padding: 100px 0;
    background-color: #DBD5C9;
    position: relative;
}
#imain::before{
    content: '';
    width: 78%;
    border-bottom: 2px solid #F93700;
    position: absolute;
    right: 0; top:23%;
}
#imain h1{
    color: #F93700;
    font-size: 5em;
    text-align: end;
    position: absolute;
    left:7%; top:43%;
    transform: rotate(270deg) translateY(100%); 
    transform-origin: 0 100%
}
#intro1{
    width: 60%;
    margin-left:35%;
    margin-top: 8%;
    padding: 0 0 50px;
    text-align: end;
}
#intro1 h2{
    color: #F93700;
    font-size:1.6em;
    padding-bottom: 30px;
}
#intro1 p b{
    font-family: sans-serif;
    display: block;
    padding: 15px 0;
    font-size:1em;
}
#intro1 p{
    font-family: sans-serif;
    color: #f5eee6;
    font-size: 1em;
    line-height: 2;
    word-break: keep-all;
    padding-left:100px;
}

#intro2{
    width: 100%;
    padding: 100px 0;
    position: relative;
}
#intro2 h2{
    color: #F93700;
    font-size:1.6em;
    position: absolute;
    left:10%; bottom:23%;
    transform: rotate(-90deg) translateY(100%);
    transform-origin: 0 100%;
}
#intro2 h2 span{
    display: block;
    border-bottom: 2px solid #a8766d;
    width:100%;
    margin-top: 10px;

}
#introtxt2{
    width: 70%;
    margin-left:15%;
    padding: 20px;
}
#introtxt2 p{
    color: #5E6554;
    font-size: 0.8em;
    line-height: 2;
    word-break: keep-all;
}
#introtxt2 p b{
    display: block;
    padding: 15px 0;
    font-size:1.4em;
    color: #5E6554;
}

#intro3{
    width: 100%; height:600px;
    padding:50px 0 100px 0;
}
#intro3 h2{
    color: #F93700;
    font-size:1.6em;
    text-align: center;
}
#intro3 h2 span{
    display:block;
    border-bottom: 2px solid #f5eee6;
    margin-top: 15px;
    width:60%;
    margin-left: 40%;
}
#introtxt3{
    width: 80%; height: 100%;
    margin: 0 auto;
    padding: 100px 100px 0 100px
}
#introtxtleft{
    float: left;
    width: 48%;
}
#introtxtleft p{
    color: #f5eee6;
    font-size: 0.8em;
    line-height: 2;
    word-break: keep-all;
}
#introtxtleft p b{
    display: block;
    color: #f5eee6;
    padding: 15px 0;
    font-size: 1.4em;
}
#introtxtright{
    float: right;
    width: 48%;
    padding:65px 20px 0;
}
#introtxtright p{
    color: #f5eee6;
    font-size: 0.8em;
    line-height: 2;
    word-break: keep-all;
}