@import url('./Common.css');

#about{
    width: 100%; height: 100%;
    margin-top:80px;
  }
#amain{
  width: 100%; height: 100%;
  padding: 100px 0;
  background-color: #fff;
  position: relative;
}
#amain::before{
  content: '';
  width: 78%;
  border-bottom: 2px solid #F93700;
  position: absolute;
  right: 0; top:20%;
}
#amain h1{
  color: #F93700;
  font-size: 5em;
  text-align: end
  ;
  position: absolute;
  left:7%; top: 15%;
  transform: rotate(270deg) translateY(100%); 
  transform-origin: 0 100%;
}
#atit{
  width:50%;
  margin-left:35%;
  margin-top: 8%;
  padding-bottom: 100px;
  text-align: end;

  display: flex;
  position: relative;
}

#atxt{
  width: 50%;
  color: #919191;
  padding-top: 230px;
}
#atxt h3{
  font-size: 1.5em;
  line-height: 1.5;
}
#atxt p{
  line-height: 1.5;
}

#aimg{
  width: 50%;
}
#aimg img{
    width:350px; height: 400px;
    object-fit: cover;
    overflow: hidden;
}
#aimg h2{
  position: absolute;
  right:17%; bottom: 3%;
  color: #F93700;
  font-size:9em;
}

#asub{
  width: 100%; height: 100vh;
  padding: 100px 0;
}
#aleft{
  float: left;
  width: 50%; height: 100%;
  margin-left: 8%;
}
#aleft div{
  padding: 20px;
}
#aleft div h3{
  color: #F93700;
  font-size: 1.2em;
}
#aleft div p{
  font-family: sans-serif;
  color: #fff;
  font-size: 0.9em;
  line-height: 2.2;
  
}
#aright{
  float: right;
  width: 30%; height: 100%;
  margin-right: 8%;
}
#aright div{
  padding: 20px;
}
#aright div h3{
  color: #F93700;
  font-size: 1.2em;
}
#aright div p{
  color: #fff;
  font-size: 0.9em;
  line-height:2.2;
}

#afoot{
  width: 100%;
  display: flex;
}
#afootmain{
  background-color: #F93700;
  width: 75%;
  padding: 150px 0;
  position: relative;
}
#afootmain::before{
  content: '';
  border-bottom: 2px solid #fff;
  width: 90%;
  position: absolute;
  left: 0; top:25%;
}
#afootmain h2{
  font-size:4.5em;
  color: #fff;
  padding-right:10%;
  text-align: end;
}
#afootmain a{
  position: absolute;
  bottom: 25%; right: 30%;
  background-color: #DBD5C9;
  padding: 5px 35px;
  border-radius: 30px;
  color: #F93700;
}
#afootmain a:hover{
  background-color: #F93700;
  border: 2px solid #fff;
  color: #fff;
}
#afootsub{
  background-color: #fff;
  width: 25%;
}
#afootsub span{
  display: flex;
  justify-content: center;
  padding: 50px 0;
}
#afootsub span a{
  color: #F93700;
  font-size: 3em;
}
#afootsub:hover{
  background-color: #F93700;
}
#afootsub:hover span a{
  color: #fff;
}