@import url('./Common.css');

#project{
    width: 100%; height: 100%;
    margin-top: 100px;
  }
  #plist{
  text-align: end;
  margin-right: 12%;
  padding: 20px 0;
  }
  #plist a{
    background-color: #a8766d;
    border-radius: 12px;
    padding: 8px 50px;
    display: inline-block;
    color: #fff;
    font-size: 0.8em
  }
  #plist a b{
    margin-left: 20px;
  }
  
  #pmain{
    width: 100%;
    height:500px;
    margin-bottom: 50px;
  }
  #pmain::after{
    content: '';
    display: inline-block;
    border-bottom: 2px solid #5E6554;
    width: 60%;
    margin:0 20%;
    margin-top: 40px;
  }
  #pleft{
    float: left;
    margin-left:12%;
    margin-top:2%;
    width: 40%;
  }
  #pleft img{
    width:580px; height: 350px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #pleft p{
    color: #5E6554;
    text-align: end;
    padding:10px 50px 20px;
    font-size: 1.2em;
  }
  #pright{
    float: right;
    margin-right:12%;
    width:30%;
  }
  #prighth1{
    font-size:2.2em;
    text-align: end;
    color: #a8766d;
  }
  #prighth1 b{
    font-size:1.8em;
  }
  #prighttxt{
  width: 100%;
  margin-top: 30px;
  }
  
  #prighttxt div{
    margin-right: 10px;
    border-bottom: 1px solid #5E6554;
    text-align: end;
    padding: 20px 0;
  }
  #prighttxt p{
    color: #5E6554;
    font-size: 1.2em;
  }
  #prighttxt p a{
    display: inline-block;
    margin-left: 20px;
    transform: rotate(90deg);
  }
  #prighttxt span{
    color: #5E6554;
    font-size: 1em;
    padding-right: 20px;
    line-height: 2;
    display: none;
  }
  
  #psub{
    width: 100%; height: 100%;
  }
  
  #suba{
    width: 100%; height: 500px;
    padding-top: 100px;
  }
  #subaleft{
    float: left;
    text-align: start;
    width: 30%;
    margin-top: 12%;
    margin-left: 20%;
  }
  #subaleft ul{
    display: flex;
    padding:0;
  }
  #subaleft ul li{
    background-color: #5E6554;
    width:40px; height: 40px;
    margin-right: 10px;
  }
  #subaleft ul li:nth-of-type(2){
    background-color:#a8766d;
  }
  #subaleft ul li:nth-of-type(3){
    background-color:#9A9B72;
  }
  #subaleft p{
    margin-top: 10px;
    color: #5E6554;
    font-size: 1.2em;
    line-height: 2;
  }
  #subaleft span{
    color: #5E6554;
    line-height:1.5;
    font-size: 0.8em;
  }
  #subaright{
    float: right;
    width: 35%;
    margin-right: 12%;
    text-align: end;
  }
  #subaright img{
    width:450px; height: 300px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #subtxt {
   width:50%;
   margin: 0 auto;
   padding: 30px 10px;
  }
  #subtxt p{
    text-align: center;
    color: #9A9B72;
    line-height: 2;
    font-size: 0.8em;
  }
  
  #subb{
    width: 100%;
    padding-top: 150px;
  }
  #subbleft{
    width:35%;
    float: left;
    margin-left: 5%;
    text-align: end;
  }
  #subbleft img{
    width:500px; height: 300px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  #subblefttxt{
    width:60%;
    padding: 20px 10px;
    text-align: end;
    margin-left:30%;
  }
  #subblefttxt p{
    color: #5E6554;
    font-size: 1.2em;
    line-height: 2;
  }
  #subblefttxt span{
    color: #9A9B72;
    font-size: 0.8em;
  }
  #subbright{
    width: 50%;
    float: right;
    padding-top:200px;
    text-align: end;
    margin-right: 5%;
    margin-bottom: 200px;
  }
  #subbright img{
    width:650px; height: 400px;
    border:10px solid #a8766d;
    border-radius:60px;
    box-sizing: initial;
    overflow: hidden;
  }
  